<template>
  <div class="my-homepage"></div>
</template>

<script>
import { store, mutations, actions } from "@/store/global.js";
import queryString from "query-string";
import HomeHeader from "@/components/HomeHeader";
let types = "大腸癌,肺癌,女性乳癌,肝癌,口腔癌,攝護腺癌,甲狀腺癌,胃癌,皮膚癌,食道癌".split(",");

export default {
  name: "home",
  data() {
    return {
      types,
      active: 0,
      cancers: [],
    };
  },
  components: {
    HomeHeader,
  },
  methods: {
    async viewList(item) {
      const obj = { id: item.cid, name: item.name };
      const str = queryString.stringify(obj);
      this.$router.push("doclist?" + str);
    },
    async getDDL() {
      this.cancers = await actions.getCancerTypes();
      store.cancers = [...this.cancers];
    },
  },
  mounted() {
    store.activeTab = 0;
    this.getDDL();
    if (!sessionStorage.token) {
      this.$router.push("login");
    } else {
      this.$router.push("paidlist");
    }
  },
};
</script>

